<template>
  <!-- 提现 -->
  <div class="withdraw">
    <el-row>
      <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span class="card-title">{{ $t('withdraw.staticRewards') }}</span>
          </div>
          <div class="card-middle">
            <el-row type="flex" justify="center">
              <el-col :span="6" class="icon-style">
                <el-image class="withdraw-img" :src="staticUrl" fit="fill"></el-image>
              </el-col>
              <el-col :span="6" class="withdraw-amount">
                <div class="eth-style">{{ balance === '--' ? '--' : balance.staticBalance }} ETH</div>
                <!-- <div class="usdt-style">24800USDT</div> -->
              </el-col>
            </el-row>
          </div>
          <div class="middleborder"></div>
          <div class="withdraw-input">
            <el-input
              type="number"
              :placeholder="$t('purchase.placeholder')"
              v-model="inputValue.static"
            ></el-input>
          </div>
          <div class="withdraw-btn">
            <el-button
              round
              @click="reinvestRewards('static')"
              :disabled="balance.staticBalance > 0 ? false : true"
            >{{ $t('commom.btnReinvest') }}</el-button>
            <el-button
              round
              @click="withdrawRewards('static')"
              :disabled="balance.staticBalance > 0 ? false : true"
            >{{ $t('commom.btnWithdraw') }}</el-button>
          </div>
        </el-card>
      </el-col>

      <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span class="card-title">{{ $t('withdraw.recommendedRewards') }}</span>
          </div>
          <div class="card-middle">
            <el-row type="flex" justify="center">
              <el-col :span="6" class="icon-style">
                <el-image class="withdraw-img" :src="recommendedUrl" fit="fill"></el-image>
              </el-col>
              <el-col :span="6" class="withdraw-amount">
                <div class="eth-style">{{ balance === '--' ? '--' : balance.recommendBalance }} ETH</div>
              </el-col>
            </el-row>
          </div>
          <div class="middleborder"></div>
          <div class="withdraw-input">
            <el-input
              type="number"
              :placeholder="$t('purchase.placeholder')"
              v-model="inputValue.recommended"
            ></el-input>
          </div>
          <div class="withdraw-btn">
            <el-button
              round
              @click="reinvestRewards('recommended')"
              :disabled="balance.recommendBalance > 0 ? false : true"
            >{{ $t('commom.btnReinvest') }}</el-button>
            <el-button
              round
              @click="withdrawRewards('recommended')"
              :disabled="balance.recommendBalance > 0 ? false : true"
            >{{ $t('commom.btnWithdraw') }}</el-button>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <el-row>
      <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span class="card-title">{{ $t('withdraw.nodeRewards') }}</span>
          </div>
          <div class="card-middle">
            <el-row type="flex" justify="center">
              <el-col :span="6" class="icon-style">
                <el-image class="withdraw-img" :src="nodeUrl" fit="fill"></el-image>
              </el-col>
              <el-col :span="6" class="withdraw-amount">
                <div class="eth-style">{{ balance === '--' ? '--' : balance.nodeBalance }}ETH</div>
              </el-col>
            </el-row>
          </div>
          <div class="middleborder"></div>
          <div class="withdraw-input">
            <el-input
              type="number"
              :placeholder="$t('purchase.placeholder')"
              v-model="inputValue.node"
            ></el-input>
          </div>
          <div class="withdraw-btn">
            <el-button
              round
              @click="reinvestRewards('node')"
              :disabled="balance.nodeBalance > 0 ? false : true"
            >{{ $t('commom.btnReinvest') }}</el-button>
            <el-button
              round
              @click="withdrawRewards('node')"
              :disabled="balance.nodeBalance > 0 ? false : true"
            >{{ $t('commom.btnWithdraw') }}</el-button>
          </div>
        </el-card>
      </el-col>
      <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span class="card-title">{{ $t('withdraw.terminatorRewards') }}</span>
          </div>
          <div class="card-middle">
            <el-row type="flex" justify="center">
              <el-col :span="6" class="icon-style">
                <el-image class="withdraw-img" :src="recommendedUrl" fit="fill"></el-image>
              </el-col>
              <el-col :span="6" class="withdraw-amount">
                <div class="eth-style">{{ balance === '--' ? '--' : balance.terminatorBalance }} ETH</div>
              </el-col>
            </el-row>
          </div>
          <div class="middleborder"></div>
          <div class="withdraw-input">
            <el-input
              type="number"
              :placeholder="$t('purchase.placeholder')"
              v-model="inputValue.terminator"
            ></el-input>
          </div>
          <div class="withdraw-btn">
            <el-button
              round
              @click="reinvestRewards('terminator')"
              :disabled="balance.terminatorBalance > 0 ? false : true"
            >{{ $t('commom.btnReinvest') }}</el-button>
            <el-button
              round
              @click="withdrawRewards('terminator')"
              :disabled="balance.terminatorBalance > 0 ? false : true"
            >{{ $t('commom.btnWithdraw') }}</el-button>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <el-row>
      <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
        <el-card class="box-card bottom-card">
          <div slot="header" class="clearfix">
            <span class="card-title">{{ $t('withdraw.teamRewards') }}</span>
          </div>
          <div class="card-middle">
            <div class="body-title">
              <p> {{ teamLock.isLock ? '您的Team收益于' + teamLock.lockTime + '被锁定，若三天之内没有解锁，Team收益将归零': '' }}</p>
              <!-- <p>{{ countDown.d }} Days {{ countDown.h }} : {{ countDown.m }} : {{ countDown.s }}</p> -->
            </div>
            <el-row type="flex" justify="center">
              <el-col :span="6" class="icon-style">
                <el-image class="withdraw-img" :src="teamUrl" fit="fill"></el-image>
              </el-col>
              <el-col :span="6" class="withdraw-amount">
                <div class="eth-style">{{ balance === '--' ? '--' : balance.teamBalance }} ETH</div>
              </el-col>
            </el-row>
          </div>
          <div class="middleborder"></div>
          <div class="withdraw-input">
            <el-input
              type="number"
              :placeholder="$t('purchase.placeholder')"
              v-model="inputValue.team"
            ></el-input>
          </div>
          <div class="withdraw-btn">
            <el-button
              round
              @click="reinvestRewards('team')"
              :disabled="balance.teamBalance > 0 ? (teamLock.isLock ? true : false) : true"
            >{{ $t('commom.btnReinvest') }}</el-button>
            <el-button
              round
              @click="withdrawRewards('team')"
              :disabled="balance.teamBalance > 0 ? (teamLock.isLock ? true : false) : true"
            >{{ $t('commom.btnWithdraw') }}</el-button>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { checkUrlParam } from '../Mixin/checkUrlParam'
import { urlFilter } from '../Mixin/urlFilter'
import store from '../store/index'
import ethUtils from '../utils/ethUtils'
export default {
  name: 'Withdraw',
  mixins: [checkUrlParam, urlFilter],
  data () {
    return {
      staticUrl: require('../assets/img/static.png'),
      recommendedUrl: require('../assets/img/recommended.png'),
      nodeUrl: require('../assets/img/node.png'),
      teamUrl: require('../assets/img/team.png'),
      input: '',
      inputValue: {
        static: 0,
        recommended: 0,
        team: 0,
        terminator: 0,
        node: 0
      },
      setIntervalId: ''
    }
  },
  computed: {
    balance () {
      let userBalance = this.$store.state.contract.userBalance
      if (userBalance) {
        return userBalance
      } else {
        return '--'
      }
    },
    commitMark: function () {
      return this.$store.state.metamask.commitMark
    },
    teamLock () {
      const lock = this.$store.state.contract.teamLock
      console.log('获取的teamlock锁定状态==================', lock)
      return lock
    }
  },
  watch: {
    commitMark: function (newVal) {
      // 因为purchase页面的reinvest操作，将withdraw页面数据获取，转移至purchase
      const referralAddress = this.checkUrlParam()
      if (!referralAddress) {
        // alert('通过邀请链接的才具有参与资格')
        return
      }
      const status = this.$store.state.metamask.status
      if (status === 1) { // 没有配合事件订阅，每次进入组件重新获取数据
        console.log('开始dispatch获取getUserBalance在purchase页面')
        this.$store.dispatch('contract/getUserBalance')
        if (this.setIntervalId === '') {
          const balanceInterval = this.$ethConf.balanceInterval
          this.setIntervalId = setInterval(function () {
            console.log('执行定时器==================12324335634564')
            store.dispatch('contract/getUserBalance')
          }, balanceInterval) // 3秒更新一次余额
        }
        this.$store.dispatch('contract/isLockWithdraw') // todo 判断team 是否被锁定
      }
    }
  },
  methods: {
    reinvestRewards (type) {
      this.withdrawVue(type, 'reinvest') // 'reInvest'调用的合约方法名 todo 这里是否需要await?待定
    },
    withdrawRewards (type) {
      this.withdrawVue(type, 'withdraw')
    },
    async withdrawVue (type, contractName) { // type: string 提现或者复投的类型，contractName: string 调用的合约名
      const referralAddress = this.checkUrlParam()
      if (!referralAddress) {
        // alert('通过邀请链接的才具有参与资格')
        this.$notify({
          title: this.$t('commom.warning'),
          message: '通过邀请链接的才具有参与资格',
          position: 'top-left',
          type: 'warning'
        })
        return
      }
      const data = {}
      const typeArr = ['static', 'recommended', 'team', 'terminator', 'node']
      const balanceTypeArr = ['staticBalance', 'recommendBalance', 'teamBalance', 'terminatorBalance', 'nodeBalance']
      data.value = typeArr.indexOf(type) + 1
      // 判断数据是否合理
      if (this.balance === '--') {
        this.$notify({
          title: '警告',
          message: '可复投金额正在加载，请稍后重试...',
          position: 'top-left',
          type: 'warning'
        })
        return
      }
      // 过滤<= 0 的数据
      data.amount = this.inputValue[type] // todo 注意 这里使用的eth,而不是wei
      this.inputValue[type] = 0 // 获取输入框中的值后，清空输入框
      if (data.amount <= 0 || data.amount > this.balance[balanceTypeArr[data.value - 1]]) {
        // alert('请输入有效的数据')
        this.$notify({
          title: this.$t('commom.warning'),
          message: '请输入有效金额...',
          position: 'top-left',
          type: 'warning'
        })
        return
      }
      // 精度过滤
      const decimalLimit = this.$ethConf.decimalLimit
      const len = data.amount.toString().split('.').length
      if (len === 2) {
        if (data.amount.toString().split('.')[1].length > decimalLimit) {
          // alert('交易精度过低，请输入有效数据')
          this.$notify({
            title: this.$t('commom.warning'),
            message: '交易精度过低，请输入有效数据',
            position: 'top-left',
            type: 'warning'
          })
          return
        }
      }
      const status = this.$store.state.metamask.status
      if (status !== 1) {
        // alert('请登录钱包')
        this.$notify({
          title: this.$t('commom.warning'),
          message: '请登陆钱包',
          type: 'warning'
        })
      } else {
        const myProvider = this.$myProvider
        const networkVersion = myProvider.networkVersion
        if (networkVersion !== this.$ethConf.networkVersion) {
          // alert('请将metamask网络切换至主网络')
          this.$notify({
            title: this.$t('commom.warning'),
            message: '请将metamask网络切换至主网络',
            type: 'warning'
          })
        } else {
          const contractRes = this.$myContracts && this.$myContracts.contractRes // Resonance合约地址  todo 标记 引用合约
          const myWeb3 = this.$myWeb3
          data.gasPrice = await myWeb3.eth.getGasPrice()
          data.gasLimit = this.$ethConf.gasLimit
          if (contractRes) {
            data.from = myProvider.selectedAddress
            console.warn('withdraw页面合约调用的参数==========', data)
            console.warn('withdraw页面调用的合约名', contractName)
            const amountHex = myWeb3.utils.toHex(ethUtils.ethToWei(data.amount)) // 统一使用十六进制,对于uint256 solidity
            const valueHex = myWeb3.utils.toHex(data.value)
            contractRes.methods[contractName](amountHex, valueHex).send({
              from: data.from,
              gasPrice: data.gasPrice,
              gas: data.gasLimit
            }, function (error, result) {
              if (error) {
                console.error('withdraw页面交易发生错误', error)
              } else {
                console.warn('withdraw页面交易返回直', result) // 返回交易hash
                store.dispatch('contract/getUserBalance')
                if (type === 'static') { // 更新isLockWithdraw, 可能会触发锁定，以及解锁
                  store.dispatch('contract/isLockWithdraw')
                }
              }
            })
          }
        }
      }
    }
  },
  created () {
    // purchase task 查询  展示, 不做提示
    const referralAddress = this.checkUrlParam()
    if (!referralAddress) {
      // alert('通过邀请链接的才具有参与资格')
      return
    }
    const status = this.$store.state.metamask.status
    if (status === 1) { // 没有配合事件订阅，每次进入组件重新获取数据
      this.$store.dispatch('contract/getUserBalance')
      if (this.setIntervalId === '') {
        const balanceInterval = this.$ethConf.balanceInterval
        this.setIntervalId = setInterval(function () {
          console.log('执行定时器==================12324335634564')
          store.dispatch('contract/getUserBalance')
        }, balanceInterval) // 3秒更新一次余额
      }
      this.$store.dispatch('contract/isLockWithdraw')
    }
  },
  beforeDestroy () {
    // 清空定时器
    clearInterval(this.setIntervalId)
    this.setIntervalId = ''
  }
}
</script>

<style lang="scss" scoped>
@import "../assets/variables.scss";
.withdraw {
  height: 100%;
  font-size: 20px;
  .box-card {
    .card-middle {
      .withdraw-amount {
        padding: 30px 0;
      }
      .icon-style {
        padding: 20px 0;
      }
      i {
        font-size: 70px;
      }
    }
  }
  .withdraw-input {
    margin: 0 auto;
    width: 50%;
  }
  .withdraw-btn {
    padding: 20px 0;
    margin: 0 auto;
    width: 260px;
  }
}
.withdraw-img {
  width: 60px;
  height: 60px;
}
.middleborder {
  margin: 0 auto 20px;
  width: 60%;
  border-bottom: 1px solid $border;
}
</style>



